import React, {Component} from 'react';

import ViewInicio from './viewInicio';
import ViewLogin from './viewLogin';

class ClassNavegar extends Component {

    constructor(props){
        super(props);
        this.setVista = this.setVista.bind(this);
        this.state = {
            vista:this.props.vista            
        }
     
    }


    setVista(vista){
        console.log(vista)
        this.setState({vista:vista})
    }


    render() {
        switch (this.state.vista) {
            case 'LOGIN':
                return (<ViewLogin usuario="" />)    
                break;
            case 'INICIO': 
                return(<ViewInicio usuario={this.props.user} vista={this.setVista} />)
                break;
            default:
                break;
        }       
    }
}


export default ClassNavegar;