import React, {Component} from 'react';
import ClassNavegar from './Navegar';

class GetViewLogin extends Component {

    constructor(props){
        super(props);
        this.user = React.createRef();
        this.pass = React.createRef();
        this.login = this.login.bind(this);
        this.state = {
            vista:"LOGIN"            
        }
    }

    login(){
        //alert(this.user.current.value + ' ' + this.pass.current.value)
        this.setState({vista: "INICIO"});
        
    }

    render() {
        if(this.state.vista=='LOGIN'){
            return (
                <div className="card col-sm-12 col-md-4 col-lg-4 col-xl-3 shadow">
                    <div className="card-body">
                        <div className="form-group text-center">
                            <img src={'./favicon.png'} width="50" height="50"></img>
                        </div>
                        <div className="form-group">
                            <label>Usuario</label>
                            <input ref={this.user} type="text" className="form-control" placeholder="Escriba su Usuario"></input>
                        </div>
                        <div className="form-group">
                            <label>Usuario</label>
                            <input ref={this.pass} type="password" className="form-control" placeholder="Escriba su Contraseña" id="txtPass"></input>
                        </div>
                        <div className="form-group">
                            <button className="btn btn-info text-white shadow btn-lg" onClick={this.login}>Ingresar</button>
                            
                        </div>
                    </div>
                </div>)
        }else{
            return (<ClassNavegar user={this.user.current.value} vista="INICIO" />)
        }
        
        
    }
}



export default GetViewLogin;